/* Stream page */

.stream-container {
    max-width: 100%;
    padding: 20px;
    display: flex;
    flex-direction: row;
    gap: 20px;
    align-items: stretch;
    position: relative;
    padding-bottom: 0;
}

.stream-player-container {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    width: 100%;
    flex: 1;
    gap: 20px;
}

.stream-player {
    max-width: 100%;
    aspect-ratio: 16/9;
}

.stream-author-profile-picture {
    background-position: center center;
    background-size: cover;
}

.stream-chat {
    position: sticky;
    position: -webkit-sticky;
    max-width: 400px;
    flex: 1 1;
    flex-shrink: 1;
    background-color: white;
    display: flex;
    flex-direction: column;
    border-radius: 6px;
    max-height: calc(100vh - 91px);
    top: 91px;
}

.stream-chat-title {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 60px;
    color: #2d806f;
    font-size: 18px;
    border-bottom: 2px solid #2d806f;
}

.stream-chat-messages {
    max-width: 100%;
    height: 100%;
    padding: 20px;
    flex-grow: 0;
    overflow-y: auto;
    overflow-wrap: break-word;
}

.stream-chat-message .username {
    color: #2d806f;
}

.stream-chat-message {
    margin-top: 5px;
    margin-bottom: 5px;
}

.stream-chat-input {
    height: 50px;
    max-width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    padding: 15px;
    gap: 6px;
}

.stream-chat-input input {
    flex: 1;
    border-radius: 6px;
    outline: none;
    border: none;
    padding: 5px 10px;
    ;
    font-size: 16px;
    background-color: rgb(236, 236, 236);
}

.stream-chat-input button {
    width: 50px;
    border-radius: 6px;
    outline: none;
    border: none;
    background-color: #2d806f;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.stream-chat-input button:hover {
    background-color: #266c5e;
}

.stream-info-container {
    max-width: 100%;
    height: auto;
    background-color: white;
    border-radius: 6px;
    padding-bottom: 15px;
    display: flex;
    flex-direction: column;
}

.stream-tabs {
    display: flex;
    flex-direction: row;
    width: 100%;
    background-color: #E2E2E2;
}

.stream-tabs div {
    padding: 5px 15px;
    background-color: #E2E2E2;
    cursor: pointer;
}

.stream-tabs div:hover {
    background-color: #cacaca;
}

.stream-tabs div:first-of-type {
    border-top-left-radius: 4px;
    background-color: #cacaca;
}

.info-tab {
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    margin-top: 15px;
}

.stream-container .stream-info-container-1 {
    padding: 0 10px;
}

.stream-container .stream-author-profile-picture {
    width: 70px;
    height: 70px;
    border-radius: 100%;
    border: 1px solid #2d806f;
    background-color: cyan;
}

.stream-container .stream-info-container-2 {
    flex: 1;
}

.stream-container .stream-title {
    font-size: 20px;
    color: #2d806f;
    margin-top: 0;
}

.stream-container .stream-author-username,
.stream-container .stream-category {
    font-size: 16px;
    color: rgb(93, 93, 93);
}

.stream-container .stream-info-container-3 {
    padding: 0 10px;
}

.stream-container .stream-author-follow {
    font-size: 16px;
    padding: 6px 10px;
    background-color: #2d806f;
    color: white;
    border-radius: 6px;
    cursor: pointer;
    display: flex;
    align-items: center;
    gap: 5px;
}

.stream-container .stream-author-follow:hover {
    background-color: #266c5e;
}

.settings {
    max-width: 100%;
    height: auto;
    padding: 0 15px;
}

.settings-title {
    font-size: 22px;
}

.settings-top {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: nowrap;
    gap: 40px;
    margin-top: 15px;
}

.settings-left {
    flex: 1;
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.settings-info-element input, .settings-info-element select {
    padding: 10px;
    background-color: #E2E2E2;
    border: none;
    outline: none;
    border-radius: 6px;
    width: 100%;
    max-width: 100%;
    cursor: pointer;
}

.settings-right {
    flex: 0.15 1;
    max-width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.settings-button {
    color: white;
    width: 100%;
    height: 100%;
    text-align: center;
    font-size: 15px;
    border-radius: 4px;
    outline: none;
    border: none;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
}

.start-button {
    background-color: #2d806f;
}

.start-button:hover {
    background-color: #266c5e;
}

.publish-button {
    background-color: rgb(255, 187, 60);
}

.publish-button:hover {
    background-color: rgb(255, 172, 18);
}

.end-button {
    background-color: #ff4a26;
}

.end-button:hover {
    background-color: #ff3810;
}

.settings-bottom {
    width: 100%;
    margin-top: 15px;
}

.flowplayer .fp-color {
    background-color: #2d806f;
}

.layouts-container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    gap: 20px;
    margin-top: 10px;
}

.layout-main {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    width: 100%;
    max-width: 300px;
    min-width: 140px;
    height: 120px;
    border: 2px solid #E2E2E2;
    border-radius: 6px;
    font-size: 22px;
    transition: 0.3s ease;
}

.layout-secondary {
    position: absolute;
    font-size: 18px;
    border-radius: 6px;
    border: 2px solid #E2E2E2;
    width: 40px;
    height: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.layout-halfs {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
}

.layout-half-left, .layout-half-right {
    width: 50%;
    height: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 18px;
    border-radius: 6px;
    border: 2px solid #E2E2E2;
}

.layout-item {
    cursor: pointer;
}

.layout-item:hover > .layout-main {
    border: 2px solid #949494;
    background-color: #949494;
    color: white;
}


.settings-bottom-2-columns {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    gap: 20px;
    width: 100%;
    position: relative;
    margin-top: 20px;
}

#publisher-canvas {
    width: 100%;
    aspect-ratio: 16/9;
    background-color: #2d806f;
}

.settings-bottom-column-2 {
    flex: 1 1;
}

.settings-bottom-column-1 {
    flex: 1 1;
}

.settings-bottom-column-2 input {
    width: 100%;
}

.settings-bottom-column-1 .settings-title, .settings-bottom-column-2 .settings-title {
    margin-bottom: 10px;
}

.chat-positions {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    gap: 20px;
}

.position-container {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    width: 100%;
    max-width: 300px;
    min-width: 140px;
    height: 120px;
    border: 2px solid #E2E2E2;
    border-radius: 6px;
    font-size: 22px;
    transition: 0.3s ease;
    cursor: pointer;
}

.chat-position-chat {
    position: absolute;
    border-radius: 6px;
    border: 2px solid #E2E2E2;
    width: 40px;
    height: 24px;
}

.chat-position-none {
    width: 40px;
    height: 40px;
    border-radius: 100%;
    border: 2px solid #E2E2E2;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
}

.chat-position-line {
    width: 100%;
    height: 0;
    border: 1px solid #E2E2E2;
    transform: rotate(-45deg);
}

.position-container:hover > .chat-position-chat {
    background-color: #949494;
    color: white;
}

.position-container:hover {
    background-color: #949494;
}

.starting-soon-player {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 50px;
    animation: starting-soon-gradient 15s ease infinite;
    padding: 10px;
}

@keyframes starting-soon-gradient {
	0% {
		background-color: #2d806f;
        color: white;
	}
    50% {
		background-color: #d4faf2;
        color: black;
	}
	100% {
		background-color: #2d806f;
        color: white;
	}
}

.colors {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    gap: 20px;
}

.color-container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    flex-wrap: nowrap;
    position: relative;
    width: 100%;
    aspect-ratio: 2/1;
    max-width: 300px;
    border: 2px solid #E2E2E2;
    border-radius: 6px;
    font-size: 22px;
    transition: 0.3s ease;
    cursor: pointer;
}

.color-container .color-1, .color-container .color-2 {
    flex: 1 1;
    width: 100%;
    height: 100%;
}

.color-container .color-1 {
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
}

.color-container .color-2 {
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
}

.color-container:hover {
    border: 2px solid #949494;
}

@media only screen and (max-width: 900px) {
    .stream-container {
        flex-direction: column;
    }

    .stream-chat {
        position: relative;
        max-height: 600px;
        height: 600px;
        top: 0;
        max-width: 100%;
        flex: 1 1;
    }
}